exports.push([module.id, "\n.active[data-v-6b6a3437] {\n  background-color: #409eff !important;\n  color: #409eff;\n}\n.details > div[data-v-6b6a3437] {\n  margin: 10px 0;\n}\n.progress[data-v-6b6a3437] {\n  /* background-color: #ebeef5; */\n  height: 10px;\n  /* border-radius: 10px; */\n  position: relative;\n\n  margin: 20px 0 10px 0;\n}\n.progress_bg[data-v-6b6a3437]:after {\n  display: block;\n  content: \"\";\n  clear: both;\n}\n.progress-item[data-v-6b6a3437] {\n  width: 10%;\n  height: 100%;\n  float: left;\n  position: relative;\n  background-color: #ebeef5;\n  border-radius: 5px;\n  cursor: pointer;\n}\n.progress-item > p[data-v-6b6a3437] {\n  position: absolute;\n  top: -35px;\n  text-align: center;\n  width: 100%;\n}\n.ApplyList[data-v-6b6a3437] .el-radio {\n  margin-right: 10px;\n}\n[data-v-6b6a3437] .el-table__body-wrapper table th {\n  padding: 0;\n}\n[data-v-6b6a3437] .el-table__body-wrapper table td {\n  font-size: 12px;\n}\n.center[data-v-6b6a3437] {\n  text-align: center;\n}\n.name[data-v-6b6a3437] {\n  width: 100%;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n.shuju[data-v-6b6a3437] {\n  margin-top: 16px;\n  font-size: 16px;\n  text-align: center;\n  color: #999;\n  overflow: hidden;\n}\n.shuju > ul > li[data-v-6b6a3437] {\n  width: 50%;\n  margin-bottom: 50px;\n}\n.shuju .son[data-v-6b6a3437] {\n  margin-left: 20px;\n  display: inline-block;\n  vertical-align: top;\n}\n.shuju .son li[data-v-6b6a3437] {\n  display: block;\n}\n.shuju .grandson[data-v-6b6a3437] {\n  margin-left: 20px;\n  vertical-align: top;\n  display: inline-block;\n}\n.shuju .grandson li[data-v-6b6a3437] {\n  display: block;\n  margin-bottom: 7px;\n}\n.details_content[data-v-6b6a3437]:after {\n  display: block;\n  content: \"\";\n  clear: both;\n}\n.details[data-v-6b6a3437] .el-dialog__header {\n  text-align: center !important;\n}\n.details[data-v-6b6a3437] .el-dialog__body {\n  overflow: inherit !important;\n}\n.details_content[data-v-6b6a3437] {\n  width: 100%;\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -ms-flex-pack: distribute;\n      justify-content: space-around;\n\n  /*border-right: 1px dashed #04BBFC;*/\n}\n.details_content > div[data-v-6b6a3437] {\n  -webkit-box-flex: 1;\n      -ms-flex: 1;\n          flex: 1;\n}\n.details_content > div[data-v-6b6a3437]:last-child {\n  border-left: 1px dashed #04bbfc;\n}\n.right[data-v-6b6a3437] {\n  display: inline-block;\n  padding-left: 6%;\n  margin-left: 8px;\n}\n.right li[data-v-6b6a3437] {\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n  padding: 16px 80px;\n  margin: 28px 0;\n  white-space: nowrap;\n  min-width: 320px;\n  max-width: 400px;\n  border-radius: 10px;\n  -webkit-box-shadow: 0px 0px 16px rgba(112, 171, 212, 1);\n          box-shadow: 0px 0px 16px rgba(112, 171, 212, 1);\n}\n.right_border-bottom[data-v-6b6a3437] {\n  border-bottom: 1px solid #ccc;\n}\n.right_border-bottom input[data-v-6b6a3437] {\n  border: none;\n  height: 25px;\n}\n.Reject_Reason[data-v-6b6a3437] {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-align: center;\n      -ms-flex-align: center;\n          align-items: center;\n  -webkit-box-pack: center;\n      -ms-flex-pack: center;\n          justify-content: center;\n  width: 100%;\n  margin-top: 9px;\n}\n.Reject_Reason > div[data-v-6b6a3437]:last-child {\n  border-bottom: 1px solid #ccc;\n  width: 70%;\n  margin-left: 10px;\n}\n.Reject_Reason input[data-v-6b6a3437] {\n  border: none;\n  width: 100%;\n  height: 30px;\n  line-height: 30px;\n}\n.details_box[data-v-6b6a3437] {\n  height: 600px;\n  overflow: auto;\n}\n.details_box a[data-v-6b6a3437] {\n  color: #04bbfc;\n}\n.details_box-btn[data-v-6b6a3437] {\n  width: 100%;\n  text-align: center;\n  margin: 20px 0;\n}\n.details_box-btn > button[data-v-6b6a3437]:first-child {\n  margin-right: 200px;\n}\n.textPrompt[data-v-6b6a3437] {\n  padding-left: 58px;\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n  color: red;\n  font-size: 15px;\n}\n.Tips[data-v-6b6a3437] {\n  color: red;\n  margin-left: 8%;\n}\n.service[data-v-6b6a3437]:after {\n  display: block;\n  content: \"\";\n  clear: both;\n}\n.service[data-v-6b6a3437] {\n  height: 350px;\n  overflow: auto;\n}\n.service > div[data-v-6b6a3437] {\n  float: left;\n  width: calc(100% / 3);\n  height: 100%;\n  text-align: center;\n}\n.service > div > p[data-v-6b6a3437] {\n  line-height: 30px;\n}\n.radio[data-v-6b6a3437] {\n  display: block;\n  text-align: left;\n  margin: 10px 0;\n}\n.radio[data-v-6b6a3437] .el-radio__input {\n  margin-left: calc(50% - 40px);\n}\n[data-v-6b6a3437] .el-radio {\n  margin-left: 0 !important;\n}\n.service > div[data-v-6b6a3437] {\n  border-right: 1px solid #ccc;\n}\n.service > div[data-v-6b6a3437]:last-child {\n  border: none;\n}\n.right[data-v-6b6a3437] .el-input__inner,\n.right[data-v-6b6a3437] .el-select .el-input.is-focus .el-input__inner {\n  border: solid 1px white;\n}\n.youpin .el-form-item[data-v-6b6a3437] {\n  margin-bottom: 0;\n}\n", "", {"version":3,"sources":["F:/仲达集团/2020-3-18总后台管理系统/src/components/registerAudit/src/components/registerAudit/apply.vue"],"names":[],"mappings":";AAqhBA;EACA,qCAAA;EACA,eAAA;CACA;AACA;EACA,eAAA;CACA;AACA;EACA,gCAAA;EACA,aAAA;EACA,0BAAA;EACA,mBAAA;;EAEA,sBAAA;CACA;AACA;EACA,eAAA;EACA,YAAA;EACA,YAAA;CACA;AAEA;EACA,WAAA;EACA,aAAA;EACA,YAAA;EACA,mBAAA;EACA,0BAAA;EACA,mBAAA;EACA,gBAAA;CACA;AAEA;EACA,mBAAA;EACA,WAAA;EACA,mBAAA;EACA,YAAA;CACA;AACA;EACA,mBAAA;CACA;AAEA;EACA,WAAA;CACA;AACA;EACA,gBAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,YAAA;EACA,iBAAA;EACA,wBAAA;EACA,oBAAA;CACA;AACA;EACA,iBAAA;EACA,gBAAA;EACA,mBAAA;EACA,YAAA;EACA,iBAAA;CACA;AAEA;EACA,WAAA;EACA,oBAAA;CACA;AAEA;EACA,kBAAA;EACA,sBAAA;EACA,oBAAA;CACA;AAEA;EACA,eAAA;CACA;AAEA;EACA,kBAAA;EACA,oBAAA;EACA,sBAAA;CACA;AAEA;EACA,eAAA;EACA,mBAAA;CACA;AACA;EACA,eAAA;EACA,YAAA;EACA,YAAA;CACA;AACA;EACA,8BAAA;CACA;AACA;EACA,6BAAA;CACA;AACA;EACA,YAAA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,0BAAA;MAAA,8BAAA;;EAEA,qCAAA;CACA;AACA;EACA,oBAAA;MAAA,YAAA;UAAA,QAAA;CACA;AAEA;EACA,gCAAA;CACA;AAEA;EACA,sBAAA;EACA,iBAAA;EACA,iBAAA;CACA;AACA;EACA,+BAAA;UAAA,uBAAA;EACA,mBAAA;EACA,eAAA;EACA,oBAAA;EACA,iBAAA;EACA,iBAAA;EACA,oBAAA;EACA,wDAAA;UAAA,gDAAA;CACA;AACA;EACA,8BAAA;CACA;AACA;EACA,aAAA;EACA,aAAA;CACA;AACA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,0BAAA;MAAA,uBAAA;UAAA,oBAAA;EACA,yBAAA;MAAA,sBAAA;UAAA,wBAAA;EACA,YAAA;EACA,gBAAA;CACA;AAEA;EACA,8BAAA;EACA,WAAA;EACA,kBAAA;CACA;AACA;EACA,aAAA;EACA,YAAA;EACA,aAAA;EACA,kBAAA;CACA;AACA;EACA,cAAA;EACA,eAAA;CACA;AACA;EACA,eAAA;CACA;AACA;EACA,YAAA;EACA,mBAAA;EACA,eAAA;CACA;AAEA;EACA,oBAAA;CACA;AACA;EACA,mBAAA;EACA,+BAAA;UAAA,uBAAA;EACA,WAAA;EACA,gBAAA;CACA;AAEA;EACA,WAAA;EACA,gBAAA;CACA;AACA;EACA,eAAA;EACA,YAAA;EACA,YAAA;CACA;AACA;EACA,cAAA;EACA,eAAA;CACA;AACA;EACA,YAAA;EACA,sBAAA;EACA,aAAA;EACA,mBAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,eAAA;EACA,iBAAA;EACA,eAAA;CACA;AACA;EACA,8BAAA;CACA;AACA;EACA,0BAAA;CACA;AAEA;EACA,6BAAA;CACA;AACA;EACA,aAAA;CACA;AACA;;EAEA,wBAAA;CACA;AACA;EACA,iBAAA;CACA","file":"apply.vue","sourcesContent":["<template>\n  <div>\n    <ctbHead>\n      <router-link :class=\"seCurId==item.id? 'cur':''\"\n                   v-for=\"item in authList\"\n                   :key=\"item.id\"\n                   :to=\"{path:item.action,query:{id:curId}}\"\n                   :v-key=\"item.id\">\n        {{item.name}}\n      </router-link>\n    </ctbHead>\n    <div class=\"container\">\n      <div class=\"quote\">\n        <div class=\"quote-ele\"><i></i>市级配送中心-注册审核-未审核</div>\n        <div class=\"quote-nav\">\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\n                       v-for=\"item in threeAuthList\"\n                       :key=\"item.id\"\n                       :to=\"{path:item.action,query:{id:curId}}\">\n            {{item.name}}\n          </router-link>\n        </div>\n      </div>\n      <el-table :data=\"list\">\n        <el-table-column prop=\"company\"\n                         label=\"配送中心名称\"\n                         align=\"center\">\n        </el-table-column>\n        <el-table-column prop=\"phone\"\n                         label=\"联系电话\"\n                         align=\"center\">\n        </el-table-column>\n        <el-table-column prop=\"leader\"\n                         label=\"负责人\"\n                         align=\"center\">\n        </el-table-column>\n        <el-table-column label=\"地区个数\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"text\"\n                       @click=\"detailsVisible=true,areaDetails(scope.row)\">{{scope.row.regions}}</el-button>\n          </template>\n        </el-table-column>\n        <el-table-column prop=\"create_time\"\n                         label=\"申请时间\"\n                         align=\"center\">\n        </el-table-column>\n        <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"text\"\n                       @click=\"materialVisible=true,aid=scope.row.aid,id=scope.row.id,throughDetails(scope.row)\">详情</el-button>\n\n          </template>\n        </el-table-column>\n      </el-table>\n      <el-dialog title=\"地区详情\"\n                 :visible.sync=\"detailsVisible\"\n                 center>\n        <div v-if=\"arealist\"\n             class=\"details\">\n          <div>省 : {{arealist.proName}}</div>\n          <div>市 : {{arealist.cityName}}</div>\n          <div>\n            区/县 :\n            <span v-for=\"(item,index) in arealist.countyName\"\n                  :key=\"index\">{{item}}、</span>\n          </div>\n        </div>\n      </el-dialog>\n      <div ref=\"container\">\n        <!--查看详情对话框-->\n        <el-dialog title=\"物料配给-详情\"\n                   class='details'\n                   :visible.sync=\"materialVisible\"\n                   @close='close'>\n          <div class=\"details_box\">\n            <div class=\"details_content\">\n              <div class=\"youpin\">\n                <div class=\"details_content-abs\">\n                  <div class=\"center\">支付凭证</div>\n                  <div class=\"center\"><img :src=\"usecost\"\n                         ref='usecost'\n                         style=\"width: 200px;height: 100px;margin:10px 0\"></div>\n                  <el-form label-position=\"left\"\n                           style=\"margin-left:58px;padding-right:58px;box-sizing: border-box;\">\n                    <el-form-item v-show='type==1'>\n                      <el-radio-group v-model=\"choose\">\n                        <el-radio :label=\"1\">倒库</el-radio>\n                        <el-radio :label=\"2\">基数</el-radio>\n                      </el-radio-group>\n\n                    </el-form-item>\n                    <el-form-item label=\"物料配给/组\"\n                                  v-show=\"type==2 || choose==2\">\n\n                    </el-form-item>\n                    <el-form-item v-show=\"type==2 || choose==2\">\n                      <div class=\"progress\">\n                        <div :class=\"['progress-item',active[index]]\"\n                             @click=\"Selection(item)\"\n                             v-for=\"(item,index) in 10\"\n                             :key=\"index\">\n                          <p>{{item}}</p>\n                        </div>\n                      </div>\n                    </el-form-item>\n                    <el-form-item>\n\n                      <el-popover placement=\"right\"\n                                  width=\"450\"\n                                  trigger=\"click\"\n                                  @show='details()'>\n                        <el-button slot=\"reference\"\n                                   type=\"primary\"\n                                   size=\"mini\">物料详情</el-button>\n                        <div style=\"text-align:center;padding-bottom:10px\">详情</div>\n                        <el-table :data=\"choose==2 || type==2 ?  materielDetails :  old_detail\"\n                                  border\n                                  size='mini'>\n                          <el-table-column label=\"物料名称\"\n                                           align=\"center\"\n                                           prop=\"materiel\"\n                                           style=\"font-size:10px\">\n                          </el-table-column>\n                          <el-table-column align='center'\n                                           label=\"库存\"\n                                           prop=\"ration\"\n                                           v-if='type==1 && choose==1'></el-table-column>\n                          <el-table-column align='center'\n                                           label=\"剩余库存\"\n                                           prop=\"materiel_stock\"\n                                           v-if='type==1 && choose==1'></el-table-column>\n                          <el-table-column label=\"件\"\n                                           align=\"center\"\n                                           prop=\"num\"\n                                           v-if='type==2 || choose==2 '>\n\n                          </el-table-column>\n                        </el-table>\n                      </el-popover>\n                      <!-- <el-button type=\"primary\"\n                               size=\"mini\">增加配给</el-button> -->\n                    </el-form-item>\n                    <!-- <el-form-item label=\"查看区域 : \">\n\n                      <el-radio v-model=\"regionState\"\n                                @change=\"detailArea\"\n                                :label=\"7\">市级</el-radio>\n                    </el-form-item> -->\n                    <el-form-item label=\"省 : \">\n                      {{regionList.proName}}\n                    </el-form-item>\n                    <el-form-item label=\"市 : \">\n                      {{regionList.cityName}}\n                    </el-form-item>\n                    <el-form-item label=\"区/县 : \">\n\n                      <div v-if=\"isArray\">\n                        <span v-for=\"(item,index) in regionList.countyName\"\n                              :key=\"index\">{{item}}、</span>\n\n                      </div>\n                      <div v-else>\n                        {{regionList.countyName}}\n                      </div>\n\n                    </el-form-item>\n                  </el-form>\n                  <div>\n                  </div>\n                </div>\n              </div>\n              <div>\n                <!-- <div v-show=\"\n                         isReadonly\"\n                     class='Tips'>*此运营商已分配利润比例不需重新分配</div> -->\n                <ul class=\"right\">\n\n                  <!-- <li>\n                    <div>运营商利润(百分比)\n                      <span class=\"color-red\"\n                            style=\"margin-left:20px\">*最高15%</span>\n                    </div>\n                    <div class=\"right_border-bottom\">\n                      <input :readonly=\"isReadonly\"\n                             max=\"15\"\n                             type=\"number\"\n                             v-model=\"profit\">\n                    </div>\n                  </li>\n                  <li>\n                    <div>\n                      <div>推荐人身份: {{detailsList.share_type}}</div>\n                      <div class=\"name\">推荐人名称: {{detailsList.share_name}}</div>\n                    </div>\n                  </li> -->\n                  <li>\n                    <div>供应商</div>\n                    <div class=\"right_border-bottom\">\n                      <el-select v-model=\"gid\"\n                                 placeholder=\"请选择\"\n                                 v-if=\"prosyList.length\">\n\n                        <el-option v-for=\"(item, index) in prosyList\"\n                                   :key=\"index\"\n                                   :value=\"item.id\"\n                                   :label=\"item.name\">\n\n                        </el-option>\n                      </el-select>\n                      <div v-else>\n                        {{prosyList.name}}\n                      </div>\n                    </div>\n                  </li>\n                  <li>\n                    <div>送货延迟罚款</div>\n                    <div class=\"right_border-bottom\">\n                      <input type=\"text\"\n                             v-model=\"delay_fine\">\n                    </div>\n                  </li>\n                  <!-- <li>\n                    <div>维修厂工时费(百分比)</div>\n                    <div class=\"right_border-bottom\">\n                      <input type=\"text\"\n                             :readonly=\"isReadonly\"\n                             v-model=\"shop_hours\">\n                    </div>\n                  </li> -->\n                </ul>\n              </div>\n            </div>\n            <div class=\"Reject_Reason\">\n              <div>驳回理由:</div>\n              <div>\n                <input type=\"text\"\n                       placeholder='请输入驳回理由'\n                       v-model=\"Reason\">\n              </div>\n            </div>\n            <div class=\"details_box-btn\">\n              <el-button type=\"danger\"\n                         danger\n                         round\n                         @click=\"Reject\"\n                         :loading=\"rejectLoading\">驳回</el-button>\n              <el-button type=\"primary\"\n                         round\n                         @click=\"adopt\"\n                         :loading=\"adoptLoading\">通过</el-button>\n            </div>\n            <!-- <div class=\"textPrompt\">\n              <p>注：1. 运营商首次配给时，输入框的值会均分到运营库以及授信库。</p>\n              <p style=\"text-indent:30px;\">2. 运营商后需增加配给时，输入框中的值会全部增加到授信库。</p>\n            </div> -->\n          </div>\n\n          <!-- <Region-dialog :regionVisible=\"regionVisible\"\n                         @regionClose=\"regionClose\"\n                         :Province=\"Province\"></Region-dialog> -->\n\n        </el-dialog>\n      </div>\n      <!-- 分页 -->\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\n           v-show=\"list && list.length>0\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport detailsDialog from \"@/components/Dialog/Dialog\";\nimport RegionDialog from \"@/components/Dialog/RegionDialog\"\nimport Viewer from '@/utils/Viewer';\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      //分页数据\n      page: 1, //显示的页码数\n      pageCount: 1,\n      curId: 0,\n      thCurId: 0,\n      threeAuthList: [],\n      authList: [],\n      detailsVisible: false,\n      arealist: [],\n      detailsList: [],\n      regionState: 1,\n      active: ['active'],\n      materielDetails: [],\n      regionList: [],\n      isReadonly: '',\n      profit: '',\n      prosyList: [],\n      oneProsy: {},\n      delay_fine: 200,\n      shop_hours: '',\n      Reason: '',\n      adoptVisible: false,\n      regionVisible: false,\n      Province: '',\n      CurrentGroup: 1,\n      materialVisible: false,\n      usecost: '',//支付凭证\n      gid: '',\n      aid: '',\n      id: '',\n      adoptLoading: false,\n      rejectLoading: false,\n      type: 0,\n      old_aid: '',\n      cancel_id: '',\n      choose: 1, // 1倒库 2奇数\n      old_detail: [],\n      Type: ''\n\n    }\n  },\n  computed: {\n    isArray () {\n      return this.regionList.countyName instanceof Array\n    }\n  },\n  components: {\n    detailsDialog,\n    RegionDialog\n  },\n  created () {\n    this.init();\n  },\n  watch: {\n    index () {\n      this.init();\n    },\n    choose (val) {\n      this.Type = val\n\n    }\n\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/AgentAuditList/index', { token: this.token, page: this.page, status: 0 })\n        .then(res => {\n          this.list = res.data.data.list;\n          this.pageCount = res.data.data.rows;\n        }).catch(err => { })\n    },\n    //地区详情\n    async areaDetails (row) {\n      try {\n        const res = await this.$axios.post('admin/AgentAuditList/showArea', { token: this.token, aid: row.aid, id: row.id })\n        this.arealist = res.data.data\n\n      } catch (error) {\n        throw (error)\n      }\n    },\n    //通过详情\n    async throughDetails (row) {\n      try {\n        const res = await this.$axios.post(\"admin/AgentAuditList/detail\", { token: this.token, aid: row.aid, id: row.id })\n        this.usecost = res.data.data.usecost\n        this.type = res.data.data.type\n        this.Type = res.data.data.type\n        this.old_aid = res.data.data.old_aid\n        this.cancel_id = res.data.data.old_cancel_id\n        this.old_detail = res.data.data.old_detail || []\n        this.detailArea(row.id)\n        this.selectAgent(row.id, row.aid)\n        this.$nextTick(() => {\n          Viewer(this.$refs.usecost)\n        })\n      } catch (error) {\n        throw (error)\n      }\n    },\n    async details () {  //获取物料详情\n\n      try {\n        const res = await this.$axios.post('admin/AgentIncRation/commonOil', { token: this.token, number: this.CurrentGroup })\n        this.materielDetails = res.data.data || []\n      } catch (err) {\n\n      }\n    },\n    Selection (length) {\n      this.CurrentGroup = length //选择物料组\n      let item = []\n      for (let i = 0; i < length; i++) {\n        item.push('active')\n      }\n      this.active = item\n\n    },\n    async detailArea (id) {  //查看用户区域\n      try {\n        const res = await this.$axios.p